/* Scrollable.module.less */
.scrollableFill {
  position: relative;
  width: 100%;
  height: 100%;
}
.scrollable {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
}
.scrollable .container {
  display: flex;
  width: 100%;
  height: 100%;
  min-height: 0;
  flex: auto;
}
.scrollable .container [data-container-muted="true"]::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  content: '';
  z-index: 10;
}
.scrollable .content {
  background: inherit;
  position: relative;
  overflow: hidden;
  flex: 1 1 100%;
}
.scrollable .contentNative {
  -webkit-overflow-scrolling: touch;
}
